import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
// import VKConnect from '@vkontakte/vkui-connect-mock';

import Home from './panels/Home';
import Detail from './panels/Detail';

import {data as less10} from './json/less10';
import {data as from10to30} from './json/from10to30'
import {data as card} from './json/card';
import {data as from30} from "./json/from30";
import {data as zalog} from "./json/zalog";

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			poput: null
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (id) => {
		this.setState({ activePanel: id })
	};

	showPoput = (poput) => {
		this.setState({poput: poput})
	};

	render() {
		return (
			<View activePanel={this.state.activePanel} popout={this.state.poput}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} showPoput={this.showPoput}/>
				<Detail id="less10" go={this.go} data={less10} title="До 10 тыс руб" showPoput={this.showPoput}/>
				<Detail id="from10to30" go={this.go} data={from10to30} title="От 10 тыс. руб до 30 тыс. руб" showPoput={this.showPoput}/>
				<Detail id="from30" go={this.go} data={from30} title="От 30 тыс. руб" showPoput={this.showPoput}/>
				<Detail id="cards" go={this.go} data={card} title="Карта рассрочки" showPoput={this.showPoput}/>
				<Detail id="zalog" go={this.go} data={zalog} title="Кредиты под залог" showPoput={this.showPoput}/>
			</View>
		);
	}
}

export default App;
