import React from 'react';
import { Panel, ListItem, Footer, Group, Avatar, PanelHeader, List, Cell } from '@vkontakte/vkui';
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon24MoneyTransfer from '@vkontakte/icons/dist/24/money_transfer';
import Icon24MarketOutline from '@vkontakte/icons/dist/24/market_outline';

class Home extends React.Component{

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader>TopMFO</PanelHeader>
				{this.props.fetchedUser &&
				<Group title="Пользователь">
					<ListItem
						before={this.props.fetchedUser.photo_200 ? <Avatar src={this.props.fetchedUser.photo_200}/> : null}
						description={this.props.fetchedUser.city && this.props.fetchedUser.city.title ? this.props.fetchedUser.city.title : ''}
					>
						{`${this.props.fetchedUser.first_name} ${this.props.fetchedUser.last_name}`}
					</ListItem>
				</Group>}

				<Group title="Я хочу занять">
					<List>
						<Cell expandable before={<Icon24MoneyCircle />} onClick={() => this.props.go('less10')}>До 10 тыс. руб</Cell>
						<Cell expandable before={<Icon24MoneyCircle />} onClick={() => this.props.go('from10to30')}>От 10 тыс. руб до 30 тыс. руб</Cell>
						<Cell expandable before={<Icon24MoneyCircle />} onClick={() => this.props.go('from30')}>От 30 тыс. руб</Cell>
					</List>
				</Group>

				<Group title="Карты">
					<List>
						<Cell expandable before={<Icon24MoneyTransfer />} onClick={() => this.props.go('cards')}>Карта рассрочки</Cell>
					</List>
				</Group>

				<Group title="Кредиты под залог">
					<List>
						<Cell expandable before={<Icon24MarketOutline />} onClick={() => this.props.go('zalog')}>До 1 млн. руб</Cell>
					</List>
				</Group>
				<Footer>Довольных клиентов:<br />3.5к</Footer>
			</Panel>
		);
	}
}

export default Home;
