import React from 'react';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, Alert, Slider} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

class Detail  extends React.Component {

	state = {
		data: this.props.data,
		rate: 0
	};

	openUrl = (url) => {
		window.open(url)
	};
	closePoput = () => {
		this.props.showPoput(null)
	};
	showPoput = () =>{
		this.props.showPoput(
			<Alert
				actionsLayout="horizontal"
				actions={[{
					title: 'Отмена',
					autoclose: true,
					style: 'destructive'
				}, {
					title: 'Ок',
					autoclose: true,
					style: 'cancel',
					action: () => {
						this.props.showPoput(
							<Alert
								actions={[{
									title: 'Ок',
									autoclose: true,
								}]}
								onClose={this.closePoput}
							>
								<h2>Спасибо</h2>
								<h1 align="center">Ваша оценка: {this.state.rate}</h1>
								<p>Спасибо за вашу оценку, вы помогли многим нашим клиентам.</p>
							</Alert>
						);
					}
				}]}
				onClose={this.closePoput}
			>
				<h2>Оценить</h2>
				<p>Если вы пользовались услугами данной организации, оставьте отзыв для других клиентов.</p>
				<Slider
					step={1}
					min={1}
					max={10}

					onChange={value => {this.setState({rate: value})}}
				/>
			</Alert>
		)
	};

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader
					left={<HeaderButton onClick={() => this.props.go('home')}>
						{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</HeaderButton>}
				>
					{this.props.title}
				</PanelHeader>

				{this.state.data.map((item,id)=>(
					<Group key={id}>
						<Div style={{'paddingTop':'0'}} >
							<table style={{'width':'100%', 'fontSize': '20px', 'textAlign':'center', '--font-ios': '-apple-system, Helvetica Neue, Arial',
									'--font-android': 'Roboto, Arial'}}>
								<tr>
									<td style={{'textAlign':'left', 'color': '#909499', 'padding':'5px', 'paddingLeft':'0', 'fontSize': '16px'}}>
										{item.title}
									</td>
									<td style={{'textAlign':'right', 'color': '#909499', 'padding':'5px', 'paddingRight':'0','fontSize': '16px'}}>
										<span onClick={this.showPoput} style={{color: '#528bcc'}}>Рейтинг</span> <b>{item.rate} / 10</b>
									</td>
								</tr>
								<tr>
									<td colSpan="2">
										<img src={item.image} alt=""/>
									</td>
								</tr>
								<tr style={{'color': '#909499', 'fontSize': '15px'}}>
									<td style={{'width':'50%'}}>
										Сумма займа
									</td>
									<td>
										Срок выплаты
									</td>
								</tr>
								<tr>
									<td>
										<b>{item.sum}</b>
									</td>
									<td>
										<b>{item.srok}</b>
									</td>
								</tr>
								<tr style={{'color': '#909499', 'fontSize': '13px'}}>
									<td>
										Процентная ставка
									</td>
									<td>
										Время рассмотрения
									</td>
								</tr>
								<tr>
									<td>
										<b>{item.stavka}</b>
									</td>
									<td>
										<b>{item.time}</b>
									</td>
								</tr>
							</table>
						</Div>
						<div className='Group__description'>
							{item.discr}
						</div>

						<Div style={{display: 'flex'}}>
							<a href={item.url} style={{
								background: '#528bcc',
								width: '100%',
								height: '30px',
								paddingTop: '7px',
								textAlign: 'center',
								borderRadius: '15px',
								textDecoration: 'none',
								color: 'white',

							}}>Подать заявку</a>
						</Div>
					</Group>

				))}


			</Panel>
		);
	}
}

export default Detail;
